import IssueForm from "./components/IssueForm";
import VerifyForm from "./components/VerifyForm";

export default function HomePage() {
  return (
    <main
      style={{
        maxWidth: "820px",
        margin: "0 auto",
        padding: "2rem 1rem",
        display: "grid",
        gap: "1.5rem",
        fontFamily: "Arial, sans-serif",
      }}
    >
      <h1>Academic Record System</h1>

      <section style={{ border: "1px solid #ddd", borderRadius: "10px", padding: "1rem" }}>
        <h2>Issue Record (Admin)</h2>
        <p style={{ marginTop: 0 }}>
          Frontend sends request data only. API routes handle private key signing, blockchain
          transactions, and database persistence.
        </p>
        <IssueForm />
      </section>

      <section style={{ border: "1px solid #ddd", borderRadius: "10px", padding: "1rem" }}>
        <h2>Verify Record (Public)</h2>
        <p style={{ marginTop: 0 }}>
          Verification happens through backend API routes to preserve clean architecture and avoid
          exposing infrastructure details to clients.
        </p>
        <VerifyForm />
      </section>
    </main>
  );
}
